@media print {
  :root {
    --header-height: 0;
  }

  body {
    --color-body-background: white !important;
    --color-body-text: black !important;
    --color-heading: black !important;
  }

  .header-site,
.footer-site {
    display: none;
  }

  .gridline {
    display: none;
  }
}
.rwd-status {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  font-size: 14px;
  color: black;
}
.rwd-status:after {
  content: "xs < 576px";
  background-color: rgba(255, 224, 130, 0.6);
  display: inline-block;
  padding: 2px 8px;
}
@media (min-width: 576px) {
  .rwd-status:after {
    content: "sm < 768px";
    background-color: rgba(197, 225, 165, 0.6);
  }
}
@media (min-width: 768px) {
  .rwd-status:after {
    content: "md < 940px";
    background-color: rgba(128, 222, 234, 0.6);
  }
}
@media (min-width: 940px) {
  .rwd-status:after {
    content: "lg < 1200px";
    background-color: rgba(159, 168, 218, 0.6);
  }
}
@media (min-width: 1200px) {
  .rwd-status:after {
    content: "xl < 1600px";
    background-color: rgba(244, 143, 177, 0.6);
  }
}
@media (min-width: 1600px) {
  .rwd-status:after {
    content: "xxl > 1600px";
    background-color: rgba(255, 171, 145, 0.6);
  }
}

